'use client';

import { useState } from 'react';
import { US_STATES } from '@/types/agency';
import { US_MAP_PATHS, STATE_LABELS } from '@/lib/us-map-data';

interface USMapProps {
  onStateClick: (stateCode: string) => void;
}

export default function USMap({ onStateClick }: USMapProps) {
  const [hoveredState, setHoveredState] = useState<string | null>(null);

  const handleStateClick = (stateCode: string) => {
    onStateClick(stateCode);
    const element = document.getElementById(`state-${stateCode}`);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  };

  return (
    <div className="w-full max-w-7xl mx-auto p-4">
      <h2 className="text-3xl font-bold text-center mb-6 text-gray-800">
        Select a State
      </h2>
      <p className="text-center text-gray-600 mb-8 min-h-[28px] font-medium">
        {hoveredState 
          ? `${US_STATES[hoveredState]} - Click to view agencies`
          : 'Hover over and click on any state to view Medicare-enrolled Home Health Care Agencies'
        }
      </p>
      
      {/* Interactive US Map SVG */}
      <div className="w-full bg-gradient-to-b from-blue-50 to-blue-25 rounded-xl p-8 shadow-lg border border-gray-200">
        <svg 
          viewBox="0 0 950 600" 
          className="w-full h-auto max-w-5xl mx-auto"
          xmlns="http://www.w3.org/2000/svg"
        >
          {/* Render all states */}
          {Object.entries(US_MAP_PATHS).map(([stateCode, stateData]) => (
            <g key={stateCode}>
              <path
                d={stateData.d}
                fill={hoveredState === stateCode ? '#2563eb' : '#60a5fa'}
                stroke="#ffffff"
                strokeWidth="1.5"
                className="cursor-pointer transition-all duration-200"
                onMouseEnter={() => setHoveredState(stateCode)}
                onMouseLeave={() => setHoveredState(null)}
                onClick={() => handleStateClick(stateCode)}
                style={{
                  filter: hoveredState === stateCode 
                    ? 'drop-shadow(0 4px 6px rgba(0,0,0,0.2))' 
                    : 'none',
                  transform: hoveredState === stateCode ? 'scale(1.02)' : 'scale(1)',
                  transformOrigin: 'center'
                }}
              >
                <title>{stateData.name}</title>
              </path>
            </g>
          ))}
          
          {/* State labels */}
          {Object.entries(STATE_LABELS).map(([stateCode, coords]) => (
            <text
              key={`label-${stateCode}`}
              x={coords.x}
              y={coords.y}
              fontSize="11"
              fontWeight="600"
              fill={hoveredState === stateCode ? '#ffffff' : '#1e40af'}
              textAnchor="middle"
              className="pointer-events-none select-none font-sans"
              style={{
                textShadow: hoveredState === stateCode 
                  ? '0 1px 3px rgba(0,0,0,0.5)' 
                  : '0 1px 2px rgba(255,255,255,0.8)',
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
              }}
            >
              {stateCode}
            </text>
          ))}
        </svg>
      </div>
      
      <p className="text-center text-xs text-gray-500 mt-4">
        Click any state on the map to view Medicare-enrolled agencies
      </p>
    </div>
  );
}

